form{
flex: 1;
padding: 10px;
}

form div{
    margin-bottom: 10px;

}

form input {
    display: block;
    padding: 5px;
    width: 100%;
    height: 40px;
    border: 1px solid gray;
    border-radius:  3px ;
}

form input[type=submit]{
    cursor: pointer;
    background-color: brown;
    color: #fff;
    border: 0;
}